
.parallax {
    background-image: -webkit-gradient(linear,left top, left bottom,from(transparent), color-stop(50%, transparent), to(white)),url("https://c1.staticflickr.com/9/8082/8297642349_3f1116278f_b.jpg");
    background-image: -webkit-linear-gradient(top,transparent 0%, transparent 50%, white),url("https://c1.staticflickr.com/9/8082/8297642349_3f1116278f_b.jpg");
    background-image: -o-linear-gradient(top,transparent 0%, transparent 50%, white),url("https://c1.staticflickr.com/9/8082/8297642349_3f1116278f_b.jpg");
    background-image: linear-gradient(to bottom,transparent 0%, transparent 50%, white),url("https://c1.staticflickr.com/9/8082/8297642349_3f1116278f_b.jpg");
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    opacity: .85;
}



